<template>
<div>
    <base-dialog :show="!!error" title="Error has occurred" @close="closeModal">
        <p>{{error}}</p>
    </base-dialog>
    <base-dialog :show="isLoading" title="Authenticating...">
        <p>Authenticating...</p>
        <base-spinner/>
    </base-dialog>
    <base-card>
        <form @submit.prevent="submitForm">
            <div class="form-control">
                <label for="email">Email</label>
                <input type="text" id="email" v-model.trim="emailAddress">
            </div>
            <div class="form-control">
                <label for="password">Password</label>
                <input type="password" id="password" v-model.trim="userPassword">
            </div>
            <p v-if="!formIsValid">Please enter valid email and password (Password must be at least more than 6 characters long)</p>
            <base-button>{{mode}}</base-button>
            <base-button type="button" mode="flat" @click="switchAuthMode">{{getMode}}</base-button>
        </form>
    </base-card>
</div>
</template>

<script>
import { mapActions } from 'vuex';

export default{
    data(){
        return{
            emailAddress:"",
            userPassword:"",
            formIsValid:true,
            mode:"Login",
            isLoading:false,
            error:null
        }
    },
    computed:{
        getMode(){
            if(this.mode ==="Login"){
                return "Sign Up Instead";
            }
            return "Log In Instead";
        }
    },
    methods:{
        ...mapActions({
            login:"loginModeActions",
            signUp:"signUpModeActions"
        }),
        closeModal(){
            this.error = false;
        },
        async submitForm(){
            this.formIsValid = true;

            // Simple Validation
            if(!this.emailAddress || !this.emailAddress.includes("@") || !this.userPassword || this.userPassword.length < 6){
                this.formIsValid = false;
                return;
            }
            this.isLoading = true;


            // If Validation passed
            // Send HTTP Request To the Firebase Server
            try{
                if(this.mode === "Login"){
                    await this.login(
                    {
                        email: this.emailAddress,
                        password: this.userPassword,
                    }
                )
                return;
                }
                else{
                    await this.signUp(
                        {
                            email: this.emailAddress,
                            password: this.userPassword,
                        }
                    )
                }
            }catch(error){
                this.error = error.message || "Failed to authenticate";
            }finally{
                if(!this.error){
                    const redirect = this.$route.query.redirect;
                    this.emailAddress = this.userPassword = "";
                    if(this.mode === "Login"){
                        this.$router.replace(`/${(redirect || "coaches" )}`);
                    }
                    this.mode = "Login";
                }
                this.isLoading = false;
            }
        },
        switchAuthMode(){
            if(this.mode === "Login"){
                this.mode = "Signup";
                return;
            }
            this.mode = "Login";
        }
    }
}
</script>

<style scoped>

form {
  margin: 1rem;
  border-radius: 12px;
  padding: 1rem;
}

.form-control {
  margin: 0.5rem 0;
}

label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

input,
textarea {
display: block;
    width: 100%;
    font: inherit;
    border: 1px solid #ccc;
    padding: 0.15rem;
}

input:focus,
textarea:focus {
    border-color: #3d008d;
    background-color: #faf6ff;
    outline: none;
}   
</style>